@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes beat {
  0% {
    width:4vh;
    height: 4vh;
    margin: 0.5vh 1vh 0.5vh;
    background-color: #00888f;
    box-shadow: 0 0 0 0.4vh #00888f;
  }
  2% {
    width: 3vh;
    height: 3vh;
    margin: 1.25vh 1.5vh 1.25vh;
    background-color:#0d161c;
    box-shadow: 0 0 0 0.4vh #0d161c;
  }
  6% {
    width: 4vh;
    height: 4vh;
    margin: 0.5vh 1vh 0.5vh;
    background-color: #00888f;
    box-shadow: 0 0 0 0.4vh #00888f;
  }
}
.appearSlowly{
  animation: 5s linear appearSlowly forwards;
}
@keyframes appearSlowly {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  75% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}






.FormGroup {
  margin: 0 15px 20px;
  padding: 0;
  border-style: 2px black;
  background-color: black;
  will-change: opacity, transform;
  border-radius: 0.5vw;
  overflow: hidden;
}

.FormRow {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-left: 15px;
  background-color: black;
  border: none;
}
.StripeElement--webkit-autofill {
  background: transparent !important;
}

.StripeElement {
  width: 100%;
  padding: 11px 15px 11px 0;
}