}
@keyframes appearSlowly {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  75% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideIn {
  0% {
    bottom:-100vh;
  }
  50% {
    bottom:-100vh;
  }
  75% {
   bottom:-50vh;
  }
  100% {
    bottom:18vh;
  }
}
@keyframes swing {
	0%{
		transform: translate(-2.5vw,0);
	}
	50%{
		transform: translate(2.5vw,0);
	}
	100%{
		transform: translate(-2.5vw,0);
	}
}

@keyframes sqTOcir {
	0%{
		transform: rotate(0deg);
	}
	13%{
		transform: rotate(0deg);
	}
	14%{
		transform: rotate(-2.5deg);
	}
	15%{
		transform: rotate(2.5deg);
	}
	16%{
		transform: rotate(-2.5deg);
	}
	17%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(0deg);
	}
}
@keyframes swingOnce {
	0%{
		transform: rotate(0deg);
	}
	25%{
		transform: rotate(-2.5deg);
	}
	50%{
		transform: rotate(2.5deg);
	}
	75%{
		transform: rotate(-2.5deg);
	}
	100%{
		transform: rotate(0deg);
	}
}
@keyframes slideInServBtn {
  0% {
    bottom:-10vh;
  }
  100% {
    bottom: 5vh;
  }
}
@keyframes slideInIconBar {
  0% {
    bottom:-10vh;
  }
  100% {
    bottom: 12vh;
  }
}